import React from 'react';

import './card.scss';

const Card = (props) => (
  <div className="col-md-2">
    <div className='card-box'>
      <div className='img-container' style={{backgroundImage: `url(https://picsum.photos/20${props.idx}/300/?random)`}}>
        <div className='text-container pl-2 pr-2'>
          <div>{props.title}</div>
          <div className='sub-text-container'>
            <div className='sub-text-container-genre'>Comedy</div>
            <div className='sub-text-container-time'>32 min</div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Card;



